import { Link } from "react-router-dom";
import ListItems from "../Components/ListItems";
import "isomorphic-unfetch";

import {
  container,
  rightSubContaoner,
  tableContaoner,
  btnSubmit,
} from "../style";

const markerStyle = {
  position: "relative",
  // top: "100%",
  // left: "50%",
  width: "100px",
  border: "2px solid red",
  // transform: "translate(-50%, -100%)",
};
import { postTransaction, getTransaction, getPayload } from "../store";
// import TransactionDetail from "./transactionDetail";

class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isFetching: false,
    };
  }

  static defaultProps = {
    center: {
      lat: 1.371778,
      lng: 103.893059,
    },
    zoom: 18,
  };

  static async getInitialProps({ reduxStore, req }) {
    return {};
  }
  componentDidMount() {
    this.loadData();
  }

  loadData = async () => {
    const id = "5d050de87eacc2395cdf86dd";
    const res = await fetch(`http://localhost:4000/getTrans/${id}`);
    const data = await res.json();
    const { getTransaction } = this.props;
    getTransaction(data);
  };
  getItemDetails = (item, type) => {
    this.props.getPayload(item, type);
  };

  handleTransfer = async () => {
    await fetch("http://localhost:4000/addTrans", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(this.props.payload),
    });
    this.props.postTransaction();
    this.loadData();
  };

  render() {
    const { transactions, payload } = this.props;
    console.log("transactions", transactions);
    //  const dataMap = {
    //   center: {
    //     lat: 1.371778,
    //     lng: 103.893059,
    //   },
    //   zoom: 18,
    // };

    return (
      <div style={container}>
        <div style={rightSubContaoner}>
          <input
            type="button"
            value="Transfer"
            // disabled={!payload.amount}
            style={btnSubmit}
            onClick={this.loadData}
          />
        </div>
        <div style={tableContaoner}>
          {/* <TransactionDetail /> */}
          <ListItems items={transactions} />
          <div style={{ height: "100vh", width: "100%" }}>
            <GoogleMapReact
              bootstrapURLKeys={{
                key: "AIzaSyA16d9FJFh__vK04jU1P64vnEpPc3jenec",
              }}
              defaultCenter={this.props.center}
              defaultZoom={this.props.zoom}>
              <div style={markerStyle}>PINq mdbjshdjhdjhsjdhjshdjshdjshj</div>
            </GoogleMapReact>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  payload: state.payload,
  transactions: state.transactions,
});

const mapDispatchToProps = {
  getTransaction,
  postTransaction,
  getPayload,
};
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Index);
